import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import {useState, useEffect} from 'react'
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  TimeScale
} from 'chart.js';
import { Line } from 'react-chartjs-2'
import {DateTime} from 'luxon'
import 'chartjs-adapter-luxon'
const axios = require('axios').default;

ChartJS.register(
  CategoryScale,
  LinearScale,
  TimeScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

function App() {

  let [data, setData] = useState([{value: ""}])
  let [boolData, setBoolData] = useState([{value: ""}])
  let [labels, setLabels] = useState([])
  let [chartData, setChartData] = useState([])
  let [boolChartData, setBoolChartData] = useState([])
  useEffect(() => {
    axios.get('https://salty-lowlands-05613.herokuapp.com/api')
        .then(response => {
          console.log(response.data)
          setData(response.data.filter(el => el.variable === 'Pressure'))
          setBoolData(response.data.filter(el => el.variable === 'Boolean'))
        })
        .catch(error => {
          console.log(error)
        })
  }, [])

  useEffect(() => {

    let new_labels = (data.map(el => {let d= DateTime.fromMillis(Number(el.date)); return d.toLocaleString(DateTime.TIME_SIMPLE)}))
    let new_chart_data = data.map(el => {return {y:el.value,x:DateTime.fromMillis(Number(el.date))}})
    let new_bool_chart_data = boolData.map(el => {return {y:el.value,x:DateTime.fromMillis(Number(el.date))}})
    
    setLabels(new_labels);
    setChartData(new_chart_data);
    setBoolChartData(new_bool_chart_data);
  }, [data]);
  return(
    <div className='container-fluid'>
      <div class = 'title h3 text-center py-2'>
        Dashboard
      </div>
      <div className='py-2 mx-2'>
      <div className = 'device-title'>Pressure Controller </div>
      <h6>Latest Readings</h6>
      <table>
        <thead>
          <td>
            Variable
          </td>
          <td>
            Reading
          </td>
          <td>
            Time
          </td>
        </thead>
        <tbody>
          <tr>
            <td>
              Pressure
            </td>
            <td>
            {data[data.length -1].value} Pa
            </td>
            <td>
            {DateTime.fromMillis(Number(data[data.length -1].date)).toLocaleString(DateTime.TIME_SIMPLE)}
            </td>
          </tr>
          <tr>
            <td>
              Boolean
            </td>
            <td>
            {boolData[boolData.length -1].value}
            </td>
            <td>
            {DateTime.fromMillis(Number(boolData[boolData.length -1].date)).toLocaleString(DateTime.TIME_SIMPLE)}
            </td>
          </tr>
        </tbody>
      </table>
      
      </div>
      <div className='col-md-6 py-2 mx-2'>
      <h6>Pressure (Pa) Graph </h6>
      <Line data = {{
        datasets: [
          {
            data: chartData,
            label: "Pressure(Pa)",
            borderColor: '#3333ff',
            fill: true,
            yAxisID: 'y',
          },
          {
            data: boolChartData,
            label: "Boolean",
            borderColor: '#aa33ff',
            fill: true,
            yAxisID: 'y1'
          }
        ]
      }} options = {{scales: {
        y: {
          type: 'linear',
          display: true,
          position: 'left',
          title: {
            display: true,
            text: 'Pressure'
          }
        },
        y1: {
          type: 'linear',
          display: true,
          position: 'right',
          title: {
            display: true,
            text: 'Boolean'
          }
        },
        x: {
          type: 'time',
          time: {
          },
          title: {
            display: true,
            text: 'Time'
          }
        }
      }, legend: {
        display: true
      }}} />
      </div>
    </div>
  );
}

export default App;
